
body {
  font-family: 'Quattrocento', serif;
  background: #941B0C;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Oswald', sans-serif;
}

.container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 100vh;
  grid-gap: 0px 0px;
}

.bg {
  display:grid;
  width: 100%;
  height: 100%;
  grid-column: 1 / 1;
  grid-row: 1 / 1;
  z-index: 0;
}


.inner {
  position: relative;
  z-index: 1;
  font-size: large;
  justify-self: center;
  -ms-flex-item-align: center;
      align-self: center;
  text-align: center;
  color: white;
}



